html, body {
  overflow-x: hidden;
}

body {
  background: #FF0099;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #493240, #FF0099);
  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#493240), to(#FF0099));
  background: -webkit-linear-gradient(left, #493240, #FF0099);
  background: -o-linear-gradient(left, #493240, #FF0099);
  background: linear-gradient(to right, #493240, #FF0099);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  margin: 0 auto;
}

.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.flex-across {
  -ms-flex-pack: start;
      justify-content: flex-start;
  position: relative;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.item-center {
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.flex-right {
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  max-width: 200px;
  margin-left: auto;
  margin-right: 10px;
}

@font-face {
  font-family: 'namco regular';
  font-style: normal;
  font-weight: normal;
  src: local('namco regular'), url('namco__.woff') format('woff');
}

h1 {
  font-family: 'namco regular';
  border-bottom: black solid 4px;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 20px;
    ;
  }
}

div#tips {
  margin-top: 200px;
  max-width: 200px;
  font-size: 15px;
  height: 200px;
  padding: 20px;
  background-color: rgba(100, 0, 255, 0.2);
  border-radius: 20px;
  -webkit-box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
          box-shadow: 4px 4px 8px 0px rgba( 0, 0, 0, 0.2);
}

div#tips p:first-child {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  border-bottom: dashed 1px black
}


